import { request } from '@/common/request.js'

export default {
	// 登录数据
	getLoginData(phone, password) {
		return request({
			url: 'account/login',
			data: {
				username: phone,
				password
			}
		})
	},
	
	// 获取订单数据
	getOrderNumData() {
		return request({
			method: 'POST',
			url: 'deliveryorder/statistics'
		})
	}
}
